<template>
  <AppModal
    v-model="isModalVisible"
    :close="closeModal"
    @beforeEnter="setState"
    @afterLeave="clearState"
  >
    <div class="modal category">
      <button class="modal_close" @click="closeModal"><span></span></button>
      <transition name="fade" mode="out-in">
        <!-- LOADER -->
        <div class="modal_content loader" v-if="isLoaderVisible">
          <AppIcon icon="loader-oval" :size="50" />
        </div>

        <!-- CATEGORY FORM -->
        <div class="modal_content" v-else>
          <h3>{{ selectedCategory ? 'Изменить' : 'Добавить' }} категорию</h3>
          <form @submit.prevent="submit" novalidate>
            <ErrorAlert v-if="submitError">
              Упс, что-то пошло не так. Пожалуйста, попробуйте еще раз.
            </ErrorAlert>
            <div class="form_group">
              <AppInput
                v-model="name"
                placeholder="Название"
                type="text"
                :invalid="v$.name.$invalid && v$.name.$dirty"
                :error="v$.name.$errors[0]?.$message"
              />
            </div>
            <div class="form_group">
              <AppSelect
                :list="listUserGroups"
                v-model="group"
                placeholder="Выберите группу"
                :clearable="false"
                :invalid="v$.group.$invalid && v$.group.$dirty"
                :error="v$.group.$errors[0]?.$message"
              />
            </div>
            <div class="form_group">
              <AppInput
                v-model="user"
                placeholder="Для пользователя"
                type="text"
                :invalid="v$.user.$invalid && v$.user.$dirty"
                :error="v$.user.$errors[0]?.$message"
              />
            </div>
            <div class="form_group category_color">
              <div
                class="category_color_box"
                :style="{
                  '--color': color,
                }"
                @click.stop="isColorPickerVisible = !isColorPickerVisible"
              ></div>
              <div
                class="category_color_picker"
                @click.stop
                v-if="isColorPickerVisible"
              >
                <ColorPicker :color="color" @changeColor="setColor" />
              </div>
              <AppInput
                v-model="color"
                placeholder="Цвет категории"
                type="text"
                class="category_color_input"
                :invalid="v$.color.$invalid && v$.color.$dirty"
                :error="v$.color.$errors[0]?.$message"
              />
            </div>
            <div class="form_group category_image">
              <div class="category_image_preview" v-if="imagePreview">
                <img :src="imagePreview" alt="" />
              </div>
              <AppInput
                v-model="image"
                :placeholder="
                  imagePreview ? 'Изменить иконку...' : 'Выбрать иконку...'
                "
                type="file"
                class="category_image_input"
                :invalid="v$.image.$invalid && v$.image.$dirty"
                :error="v$.image.$errors[0]?.$message"
              />
            </div>
            <AppButton
              color="primary"
              type="submit"
              spinner
              :loading="submitStatus === 'loading'"
            >
              Сохранить
            </AppButton>
          </form>
        </div>
      </transition>
    </div>
  </AppModal>
</template>

<script>
import { useVuelidate } from '@vuelidate/core';
import { required, email, helpers } from '@vuelidate/validators';
import { ColorPicker } from 'vue-color-kit';
import AppSelect from '@/components/AppSelect.vue';
import AppButton from '@/components/AppButton.vue';
import AppInput from '@/components/inputs/AppInput/AppInput.vue';
import ErrorAlert from '@/components/ErrorAlert.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useCategoriesStore } from '@/stores/useCategoriesStore';
import { useToaster } from '@/utils/useToaster';

const { withMessage } = helpers;

export default {
  setup() {
    return {
      v$: useVuelidate(),
      appStore: useAppStore(),
      categoriesStore: useCategoriesStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    name: '',
    group: null,
    user: '',
    color: '',
    image: null,
    imagePreview: '',
    listUserGroups: [
      {
        label: 'Для всех',
        value: 'all',
      },
      {
        label: 'Для premium',
        value: 'premium',
      },
    ],
    isColorPickerVisible: false,
    submitStatus: '',
    submitError: '',
  }),
  validations() {
    return {
      name: {
        required: withMessage(() => 'Имя не может быть пустым', required),
      },
      user: {
        email: withMessage(() => 'Указан неверный формат email', email),
      },
      group: {
        required: withMessage(() => 'Группа не может быть пустой', required),
      },
      color: {
        required: withMessage(() => 'Неверный формат цвета', this.validateHex),
      },
      image: {
        required: withMessage(() => 'Выберите иконку', this.validateImage),
      },
    };
  },
  methods: {
    clearState() {
      this.name = '';
      this.group = null;
      this.user = '';
      this.color = '';
      this.image = null;
      this.imagePreview = '';
      this.imagePreview = '';
      this.submitStatus = '';
      this.submitError = '';
      this.categoriesStore.selectCategory(null);
      this.v$.$reset();
    },
    setState() {
      if (!this.selectedCategory) {
        return;
      }

      this.name = this.selectedCategory.name;
      this.group = this.selectedCategory.group;
      this.user = this.selectedCategory.user;
      this.color = this.selectedCategory.color;
      this.imagePreview = this.selectedCategory.image;
    },
    setColor($e) {
      this.color = $e.hex;
    },
    validateHex(value) {
      const hexColorRegex = /^#([0-9a-fA-F]{3}){1,2}$/i;
      return hexColorRegex.test(value);
    },
    validateImage(value) {
      return this.imagePreview || value;
    },
    closeModal() {
      this.appStore.showModalCategory(false);
    },
    submit() {
      if (this.selectedCategory) {
        this.updateCategory();
      } else {
        this.addCategory();
      }
    },
    async addCategory() {
      this.v$.$touch();
      if (this.v$.$invalid) {
        return false;
      }

      try {
        this.submitStatus = 'loading';

        await this.categoriesStore.createCategory({
          name: this.name,
          group_id: this.group,
          user: this.user,
          color: this.color,
          image: this.image,
        });

        await this.categoriesStore.getCategories();

        this.submitError = null;
        this.submitStatus = 'success';
        this.toaster.success('Категория успешно добавлена');
        this.closeModal();
      } catch (e) {
        this.submitStatus = 'error';
        this.submitError = e;
        console.error('Не получилось добавить категорию');
      }
    },
    async updateCategory() {
      this.v$.$touch();
      if (this.v$.$invalid) {
        return false;
      }

      try {
        this.submitStatus = 'loading';

        const data = {
          id: this.selectedCategory.id,
          name: this.name,
          group_id: this.group,
          user: this.user,
          color: this.color,
        };

        if (this.image) {
          data.image = this.image;
        }

        await this.categoriesStore.updateCategory(data);
        await this.categoriesStore.getCategories();

        this.submitError = null;
        this.submitStatus = 'success';
        this.toaster.success('Категория успешно обновлена');
      } catch (e) {
        this.submitStatus = 'error';
        this.submitError = e;
        console.error('Не получилось обновить категорию');
      }
    },

    _clickListener() {
      this.isColorPickerVisible = false;
    },
  },
  mounted() {
    document.addEventListener('click', this._clickListener);
  },
  beforeUnmount() {
    document.removeEventListener('click', this._clickListener);
  },
  computed: {
    isModalVisible() {
      return this.appStore.isModalCategoryVisible;
    },
    isLoaderVisible() {
      return false;
    },
    selectedCategory() {
      return this.categoriesStore.selectedCategory;
    },
  },
  components: {
    AppInput,
    AppButton,
    AppSelect,
    ErrorAlert,
    ColorPicker,
  },
};
</script>

<style lang="scss" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.modal_content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.loader {
  padding: 50px 0;
}
form {
  width: 100%;

  .form_group {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  .button {
    width: 100%;
    margin-top: 25px;
  }
}
.category_color {
  display: flex;
  position: relative;
}
.category_color_box {
  width: 45px;
  height: 45px;
  background: #f2f2f2;
  flex-shrink: 0;
  margin-right: 10px;
  border-radius: 15px;
  position: relative;

  &::before {
    content: '';
    border-radius: inherit;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: var(--color);
  }
}
.category_color_input {
  width: 100%;
}
.category_color_picker {
  position: absolute;
  bottom: 100%;
  margin-bottom: 10px;
  z-index: 1;
}

.category_image {
  display: flex;
}
.category_image_preview {
  width: 45px;
  height: 45px;
  flex-shrink: 0;
  margin-right: 10px;
  background: #f2f2f2;
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: 50%;
    height: 50%;
  }
}
.category_image_input {
  width: 100%;
  min-width: 0;
}
</style>
