<template>
  <AppModal
    v-model="isModalVisible"
    :close="closeModal"
    @beforeEnter="setState"
    @afterLeave="clearState"
  >
    <div class="modal colorScheme">
      <button class="modal_close" @click="closeModal"><span></span></button>
      <div class="modal_content">
        <h3>
          {{ selectedColorScheme ? 'Изменить' : 'Добавить' }} цветовую схему
        </h3>
        <form @submit.prevent="submit" novalidate>
          <ErrorAlert v-if="submitError">
            Упс, что-то пошло не так. Пожалуйста, попробуйте еще раз.
          </ErrorAlert>
          <div class="form_group colorScheme_color">
            <div
              class="colorScheme_color_box"
              :style="{
                '--color': color,
              }"
              @click.stop="isColorPickerVisible = !isColorPickerVisible"
            ></div>
            <div
              class="colorScheme_color_picker"
              @click.stop
              v-show="isColorPickerVisible"
            >
              <ColorPicker
                :color="color"
                @colorChange="setColor"
                defaultFormat="hex"
              />
            </div>
            <AppInput
              v-model="color"
              placeholder="Основной цвет"
              type="text"
              class="colorScheme_color_input"
              :invalid="v$.color.$invalid && v$.color.$dirty"
              :error="v$.color.$errors[0]?.$message"
            />
          </div>
          <div class="form_group colorScheme_usedColors">
            <h4>Используемые цвета:</h4>
            <ul>
              <li v-for="color in colors" :key="color.id">
                <AppCheckbox
                  v-model="usedColors"
                  :value="color.id"
                  name="usedColors"
                >
                  {{ $t(`colors.${color.title}`) }}
                </AppCheckbox>
              </li>
            </ul>
            <span
              v-if="v$.usedColors.$invalid && v$.usedColors.$dirty"
              class="colorScheme_usedColors_error"
            >
              {{ v$.usedColors.$errors[0]?.$message }}
            </span>
          </div>
          <AppButton
            color="primary"
            type="submit"
            spinner
            :loading="submitStatus === 'loading'"
          >
            Сохранить
          </AppButton>
        </form>
      </div>
    </div>
  </AppModal>
</template>

<script>
import { useVuelidate } from '@vuelidate/core';
import { helpers } from '@vuelidate/validators';
import ColorPicker from '@/components/ColorPicker.vue';
import AppButton from '@/components/AppButton.vue';
import AppCheckbox from '@/components/AppCheckbox.vue';
import AppInput from '@/components/inputs/AppInput/AppInput.vue';
import ErrorAlert from '@/components/ErrorAlert.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useColorsStore } from '@/stores/useColorsStore';
import { useBannersStore } from '@/stores/useBannersStore';
import { useToaster } from '@/utils/useToaster';

const { withMessage } = helpers;

export default {
  setup() {
    return {
      v$: useVuelidate(),
      appStore: useAppStore(),
      colorsStore: useColorsStore(),
      bannersStore: useBannersStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    color: '#fff',
    usedColors: [],
    isColorPickerVisible: false,
    submitStatus: '',
    submitError: '',
  }),
  validations() {
    return {
      color: {
        required: withMessage(() => 'Неверный формат цвета', this.validateHex),
      },
      usedColors: {
        required: withMessage(
          () => 'Выберите хотя бы один цветовой маркер',
          this.validateUsedColors,
        ),
      },
    };
  },
  methods: {
    clearState() {
      this.color = '#fff';
      this.usedColors = [];
      this.submitStatus = '';
      this.submitError = '';
      this.bannersStore.selectColorScheme(null);
      this.v$.$reset();
    },
    setState() {
      if (!this.selectedColorScheme) {
        return;
      }

      this.color = this.selectedColorScheme.color;
      this.usedColors = this.selectedColorScheme.usedColors || [];
    },
    setColor($e) {
      this.color = $e.colors.hex;
    },
    validateHex(value) {
      const hexColorRegex = /^#([0-9a-fA-F]{3}){1,2}$/i;
      return hexColorRegex.test(value);
    },
    validateUsedColors(value) {
      return value.length;
    },
    closeModal() {
      this.appStore.showModalColorScheme(false);
    },
    submit() {
      if (this.selectedColorScheme) {
        this.updateColorScheme();
      } else {
        this.addColorScheme();
      }
    },
    async addColorScheme() {
      this.v$.$touch();
      if (this.v$.$invalid) {
        return false;
      }

      try {
        this.bannersStore.addColorScheme({
          color: this.color,
          usedColors: this.usedColors,
        });

        this.toaster.success('Цветовая схема успешно добавлена');
        this.closeModal();
      } catch (e) {
        this.submitStatus = 'error';
        this.submitError = e;
        console.error('Не получилось добавить цветовую схему');
      }
    },
    async updateColorScheme() {
      this.v$.$touch();
      if (this.v$.$invalid) {
        return false;
      }

      try {
        this.bannersStore.updateColorScheme({
          id: this.selectedColorScheme.id,
          color: this.color,
          usedColors: this.usedColors,
        });

        this.submitError = null;
        this.submitStatus = 'success';
        this.toaster.success('Цветовая схема успешно обновлена');
      } catch (e) {
        this.submitStatus = 'error';
        this.submitError = e;
        console.error('Не получилось обновить цветовую схему');
      }
    },

    _clickListener() {
      this.isColorPickerVisible = false;
    },
  },
  mounted() {
    document.addEventListener('click', this._clickListener);
  },
  beforeUnmount() {
    document.removeEventListener('click', this._clickListener);
  },
  computed: {
    isModalVisible() {
      return this.appStore.isModalColorSchemeVisible;
    },
    selectedColorScheme() {
      return this.bannersStore.selectedColorScheme;
    },
    colors() {
      return this.colorsStore.colors.data;
    },
  },
  components: {
    AppInput,
    AppButton,
    ErrorAlert,
    ColorPicker,
    AppCheckbox,
  },
};
</script>

<style lang="scss" scoped>
.modal_content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
form {
  width: 100%;

  .form_group {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  .button {
    width: 100%;
    margin-top: 25px;
  }
}
.colorScheme_color {
  display: flex;
  position: relative;
}
.colorScheme_color_box {
  width: 45px;
  height: 45px;
  background: #f2f2f2;
  flex-shrink: 0;
  margin-right: 10px;
  border-radius: 15px;
  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    border-radius: inherit;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: var(--color);
    box-shadow: 0 0 0 2px hsla(0, 0%, 0%, 0.2) inset;
  }
}
.colorScheme_color_input {
  width: 100%;
}
.colorScheme_color_picker {
  position: absolute;
  top: 100%;
  margin-top: 10px;
  z-index: 1;
  width: 100%;
}

.colorScheme_usedColors {
  margin-top: 15px;

  li {
    margin-top: 5px;
  }
}
.colorScheme_usedColors_error {
  color: var(--color-error);
