<template>
  <div class="main_column banners">
    <div
      class="categories"
      :class="{
        'is-scrolling': categoriesScrollState === 'scrolling',
        'is-scrollEnd': categoriesScrollState === 'end',
      }"
    >
      <ul v-hscroll class="categories_list" @scroll="scrollCategories">
        <li
          class="categories_list_item"
          v-for="category in categories"
          :key="category.id"
        >
          <CategoryItem :category="category" />
        </li>
      </ul>
      <div class="categories_add">
        <AppButton color="primary" @click="showModalCategory">
          Добавить категорию
        </AppButton>
      </div>
    </div>
    <div class="banners_header">
      <h2>Баннеры</h2>
      <AppButton color="primary" :to="{ name: 'addBanner' }">
        Добавить баннер
      </AppButton>
      <AppButton color="black" outlined class="banners_header_analytics">
        <img
          src="../../assets/images/charts.png"
          width="20"
          alt=""
          class="icon"
        />
        Перейти в аналитику
      </AppButton>
    </div>
    <ul v-if="banners.length" class="banners_list">
      <li v-for="banner in banners" :key="banner.key" ref="bannerItem">
        <BannerItem :banner="banner" :width="bannerItemWidth" />
      </li>
    </ul>
    <div v-else class="banners_empty">
      <img src="../../assets/images/emoji-sad-cat.png" alt="" />
      <h3>Не создано ни одного баннера</h3>
    </div>
    <ModalCategory />
  </div>
</template>

<script>
import BannerItem from '@/components/BannerItem.vue';
import CategoryItem from '@/components/CategoryItem.vue';
import ModalCategory from '@/components/ModalCategory.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useBannersStore } from '@/stores/useBannersStore';
import { useCategoriesStore } from '@/stores/useCategoriesStore';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      bannersStore: useBannersStore(),
      categoriesStore: useCategoriesStore(),
    };
  },
  data: () => ({
    categoriesScrollState: false,
    bannerItemWidth: 0,
    bannerItemResizeObserver: null,
  }),
  components: {
    BannerItem,
    CategoryItem,
    ModalCategory,
  },
  computed: {
    banners() {
      return this.bannersStore.banners.data;
    },
    categories() {
      return this.categoriesStore.categories.data;
    },
  },
  methods: {
    async init() {
      this.appStore.toggleAppLoader(true);
      await this.bannersStore.getBanners();
      this.appStore.toggleAppLoader(false);
    },
    scrollCategories($event) {
      const el = $event.target;
      const scrolledAmount = Math.ceil(el.scrollLeft + el.offsetWidth);

      if (scrolledAmount >= el.scrollWidth) {
        this.categoriesScrollState = 'end';
      } else if (el.scrollLeft > 0) {
        this.categoriesScrollState = 'scrolling';
      } else {
        this.categoriesScrollState = 'start';
      }
    },
    showModalCategory() {
      this.appStore.showModalCategory(true);
    },
  },
  watch: {
    banners: {
      deep: true,
      handler() {
        if (!this.banners.length) {
          return;
        }

        this.bannerItemResizeObserver = new ResizeObserver((entries) => {
          for (let entry of entries) {
            this.bannerItemWidth = entry.contentRect.width;
          }
        });

        this.$nextTick(() => {
          this.bannerItemResizeObserver.observe(
            this.$refs.bannerItem[0].querySelector('.banner_image'),
          );
        });
      },
    },
  },
  mounted() {
    this.init();
  },
};
</script>

