  display: flex;
  align-items: center;
  margin: 60px 0 30px;

  &_analytics {
    margin-left: auto;
  }
  h2 {
    margin-right: 30px;
  }
}
.banners_list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;

  li {
    min-width: 0;
  }
}
.banners_empty {
  display: flex;
  flex-direction: column;
  min-height: 200px;
  height: 50vh;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: rgba(0, 0, 0, 0.35);
  padding-top: 100px;

  img {
    width: 5em;
    margin-bottom: 0.5em;
  }
}

.categories {
  height: 90px;
  overflow: hidden;
  position: relative;

  &::before {
    position: absolute;
    content: '';
    width: 50px;
    top: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.075s ease;
    background: linear-gradient(
      to right,
      #f2f2f2 0%,
      rgba(242, 242, 242, 0) 100%
    );
    pointer-events: none;
  }
  &.is-scrolling,
  &.is-scrollEnd {
    &::before {
      opacity: 1;
    }
  }
  &.is-scrollEnd {
    .categories_add::before {
      opacity: 0;
    }
  }
}
.categories_list {
  display: flex;
  overflow-x: auto;
  padding-right: 210px;
  overscroll-behavior: none;
}
.categories_list_item {
  flex-shrink: 0;

  & + & {
    margin-left: 10px;
  }
}
.categories_add {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  pointer-events: none;
  display: flex;
  align-items: center;

  .button {
    pointer-events: all;
  }
  &::before {
    position: absolute;
    content: '';
    width: 450px;
    top: 0;
    bottom: 0;
    right: 0;
    transition: opacity 0.075s ease;
    background: linear-gradient(
      to left,
      #f2f2f2 25%,
      rgba(242, 242, 242, 0) 100%
    );
  }
}
</style>
