<template>
  <ColorPicker :color="color" @colorChange="colorChange" />
</template>

<script>
import { ColorPicker } from 'vue-accessible-color-picker';

export default {
  props: {
    color: {
      type: [String, Object],
    },
  },
  components: {
    ColorPicker,
  },
  methods: {
    colorChange($event) {
      $event.colors.hex = $event.colors.hex.substring(0, 7);
      this.$emit('colorChange', $event);
    },
  },
};
</script>

