<template>
  <div class="category">
    <div
      class="category_icon"
      :style="{ '--category-color': category.color || null }"
    >
      <img :src="category.image" alt="" v-if="category.image" />
      <AppIcon icon="gallery-bold" size="50%" v-else />
    </div>
    <div class="category_info">
      <h4>{{ category.name }}</h4>
      <div class="category_actions">
        <span class="category_count label">
          {{ category.banners_count || 0 }}
          {{ $tc('app.banners', category.banners_count || 0) }}
        </span>
        <AppButton icon blank color="primary" @click="editCategory">
          <AppIcon icon="pencil" size="1em" />
        </AppButton>
        <AppButton icon blank color="error" @click="deleteCategory">
          <AppIcon icon="delete" size="0.65em" />
        </AppButton>
      </div>
    </div>
    <div
      class="category_deleted"
      :class="{ 'is-visible': isDeleted || isBeingDeleted }"
    >
      <AppIcon v-if="isBeingDeleted" icon="loader-oval" size="2em" />
      <div v-else class="category_deleted_info">
        <h4>Категория удалёна</h4>
        <AppButton
          @click="restoreCategory"
          color="primary"
          spinner
          :loading="isBeingRestored"
          blank
          >Восстановить</AppButton
        >
      </div>
    </div>
  </div>
</template>

<script>
import { useAppStore } from '@/stores/useAppStore';
import { useCategoriesStore } from '@/stores/useCategoriesStore';
import { useToaster } from '@/utils/useToaster';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      categoriesStore: useCategoriesStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    isDeleted: false,
    isBeingDeleted: false,
    isBeingRestored: false,
  }),
  props: {
    category: {
      type: Object,
      required: true,
    },
  },
  methods: {
    editCategory() {
      this.categoriesStore.selectCategory(this.category);
      this.appStore.showModalCategory(true);
    },
    async deleteCategory() {
      try {
        this.isBeingDeleted = true;
        await this.categoriesStore.deleteCategory(this.category.id);
        this.isDeleted = true;
      } catch (error) {
        this.toaster.error('Не получилось удалить категорию :(');
      } finally {
        this.isBeingDeleted = false;
      }
    },
    async restoreCategory() {
      try {
        this.isBeingRestored = true;
        await this.categoriesStore.restoreCategory(this.category.id);
        this.isDeleted = false;
      } catch (error) {
        this.toaster.error('Не получилось восстановить категорию :(');
      } finally {
        this.isBeingRestored = false;
      }
    },
  },
};
</script>

