<template>
  <AppModal
    v-model="isModalVisible"
    :close="closeModal"
    @beforeEnter="setState"
    @afterLeave="clearState"
  >
    <div class="modal category">
      <button class="modal_close" @click="closeModal"><span></span></button>
      <div class="modal_content">
        <h3>{{ selectedUser ? 'Изменить' : 'Добавить' }} пользователя</h3>
        <form @submit.prevent="submit" novalidate>
          <ErrorAlert v-if="submitError">
            Упс, что-то пошло не так. Пожалуйста, попробуйте еще раз.
          </ErrorAlert>
          <div class="form_group">
            <AppInput
              v-model="name"
              placeholder="Имя"
              type="text"
              :invalid="v$.name.$invalid && v$.name.$dirty"
              :error="v$.name.$errors[0]?.$message"
            />
          </div>
          <div class="form_group">
            <AppInput
              v-model="email"
              placeholder="Email"
              type="email"
              :invalid="v$.email.$invalid && v$.email.$dirty"
              :error="v$.email.$errors[0]?.$message"
            />
          </div>
          <div class="form_group" v-if="!selectedUser">
            <AppInput
              v-model="password"
              placeholder="Пароль"
              type="password"
              :invalid="v$.password.$invalid && v$.password.$dirty"
              :error="v$.password.$errors[0]?.$message"
            />
          </div>
          <div class="form_group">
            <AppSelect
              :list="listOccupations"
              v-model="occupation"
              placeholder="Вид деятельности"
              :clearable="false"
              :invalid="v$.occupation.$invalid && v$.occupation.$dirty"
              :error="v$.occupation.$errors[0]?.$message"
            />
          </div>
          <div class="form_group">
            <AppSelect
              :list="listUserStatuses"
              v-model="status"
              placeholder="Статус"
              :clearable="false"
              :invalid="v$.status.$invalid && v$.status.$dirty"
              :error="v$.status.$errors[0]?.$message"
            />
          </div>
          <div class="form_group">
            <AppInput
              v-model="country"
              placeholder="Страна (на англ.)"
              type="text"
              :invalid="v$.country.$invalid && v$.country.$dirty"
              :error="v$.country.$errors[0]?.$message"
            />
          </div>
          <AppCheckbox v-model="emailSubscription" class="emailSubscription">
            <span class="text_h4"> Разрешить рассылку </span>
          </AppCheckbox>
          <AppButton
            color="primary"
            type="submit"
            spinner
            :loading="submitStatus === 'loading'"
          >
            Сохранить
          </AppButton>
        </form>
      </div>
    </div>
  </AppModal>
</template>

<script>
import { useVuelidate } from '@vuelidate/core';
import { required, email, minLength, helpers } from '@vuelidate/validators';
import AppSelect from '@/components/AppSelect.vue';
import AppCheckbox from '@/components/AppCheckbox.vue';
import AppButton from '@/components/AppButton.vue';
import AppInput from '@/components/inputs/AppInput/AppInput.vue';
import ErrorAlert from '@/components/ErrorAlert.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useUserStore } from '@/stores/useUserStore';
import { useToaster } from '@/utils/useToaster';

const { withMessage } = helpers;

export default {
  setup() {
    return {
      v$: useVuelidate(),
      appStore: useAppStore(),
      userStore: useUserStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    name: '',
    email: '',
    password: '',
    country: '',
    occupation: null,
    emailSubscription: true,
    status: null,
    listOccupations: [
      {
        label: 'Веб дизайн',
        value: 'web-design',
      },
      {
        label: 'Программирование',
        value: 'development',
      },
      {
        label: 'SMM менеджмент',
        value: 'smm-management',
      },
      {
        label: 'Алкоголизм',
        value: 'alcoholism',
      },
    ],
    listUserStatuses: [
      {
        label: 'Пользователь',
        value: 0,
      },
      {
        label: 'Модератор',
        value: 1,
      },
      {
        label: 'Админ',
        value: 2,
      },
    ],
    submitStatus: '',
    submitError: '',
  }),
  validations() {
    return {
      name: {
        required: withMessage(() => 'Имя не может быть пустым', required),
      },
      password: {
        required: withMessage(() => 'Пароль не может быть пустым', required),
        minLength: withMessage(({ $params }) => {
          return `Пароль должен быть не менее ${$params.min} знаков`;
        }, minLength(4)),
      },
      email: {
        required: withMessage(() => 'Email не может быть пустым', required),
        email: withMessage(() => 'Указан неверный формат email', email),
      },
      occupation: {
        required: withMessage(
          () => 'Вид деятельности не может быть пустым',
          required,
        ),
      },
      status: {
        required: withMessage(() => 'Статус не может быть пустым', required),
      },
      country: {
        required: withMessage(
          () => 'Разрешены только латинские буквы',
          this.validateCountry,
        ),
      },
    };
  },
  methods: {
    clearState() {
      this.name = '';
      this.email = '';
      this.password = '';
      this.country = '';
      this.status = null;
      this.occupation = null;
      this.emailSubscription = true;
      this.submitStatus = '';
      this.submitError = '';
      this.userStore.selectUser(null);
      this.v$.$reset();
    },
    setState() {
      if (!this.selectedUser) {
        return;
      }

      this.name = this.selectedUser.name;
      this.email = this.selectedUser.email;
      this.occupation = this.selectedUser.occupation;
      this.emailSubscription = this.selectedUser.emailSubscription;
    },
    closeModal() {
      this.appStore.showModalUser(false);
    },
    submit() {
      if (this.selectedUser) {
        this.updateUser();
      } else {
        this.addUser();
      }
    },
    async addUser() {
      this.v$.$touch();
      if (this.v$.$invalid) {
        return false;
      }

      try {
        this.submitStatus = 'loading';

        await this.userStore.createUser({
          name: this.name,
          email: this.email,
          password: this.password,
          occupation: this.occupation,
          emailSubscription: this.emailSubscription,
        });

        await this.userStore.getUsers();

        this.submitError = null;
        this.submitStatus = 'success';
        this.toaster.success('Пользователь успешно добавлен');
        this.closeModal();
      } catch (e) {
        this.submitStatus = 'error';
        this.submitError = e;
        console.error('Не получилось добавить пользователя');
      }
    },
    async updateUser() {
      this.v$.$touch();
      if (
        this.v$.name.$invalid ||
        this.v$.email.$invalid ||
        this.v$.country.$invalid ||
        this.v$.status.$invalid ||
        this.v$.occupation.$invalid
      ) {
        return false;
      }

      try {
        this.submitStatus = 'loading';

        const data = {
          id: this.selectedUser.id,
          name: this.name,
          email: this.email,
          country: this.country,
          status: this.status,
          occupation: this.occupation,
          emailSubscription: this.emailSubscription,
        };

        await this.userStore.updateUser(data);

        this.submitError = null;
        this.submitStatus = 'success';
        this.toaster.success('Пользователь успешно обновлён');
      } catch (e) {
        this.submitStatus = 'error';
        this.submitError = e;
        console.error('Не получилось обновить пользователя');
      }
    },
    validateCountry(value) {
      // only latin characters
      const validationRegex = /^[ a-z]+$/i;
      return !value ? true : validationRegex.test(value);
    },
  },
  computed: {
    isModalVisible() {
      return this.appStore.isModalUserVisible;
    },
    selectedUser() {
      return this.userStore.selectedUser;
    },
  },
  components: {
    AppInput,
    AppButton,
    AppSelect,
    AppCheckbox,
    ErrorAlert,
  },
};
</script>

<style lang="scss" scoped>
.modal_content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
form {
  width: 100%;

  .form_group {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  .button {
    width: 100%;
    margin-top: 25px;
  }
}
.emailSubscription {
  margin-top: 20px;

  .text_h4 {
    opacity: 0.5;
  }
}
</style>
