<template>
  <div class="banner">
    <div class="banner_image">
      <div class="banner_image_wrapper">
        <BannerCanvas
          :canvas="canvas"
          :scheme="banner.data.schemes[0]?.id"
          :banner="banner.data"
          :zoom="width / canvas.width"
          ref="canvas"
        />
      </div>
      <!-- <img :src="banner.preview" alt="" /> -->
      <span class="banner_category">{{ category.name }}</span>
    </div>
    <ul class="banner_info">
      <li>
        {{ banner.view_count?.toLocaleString('ru-RU') || 0 }}
        <span>Просмотров</span>
      </li>
      <li>
        {{ banner.used?.toLocaleString('ru-RU') || 0 }} раз
        <span>Использован</span>
      </li>
    </ul>
    <div class="banner_actions">
      <router-link
        :to="{ name: 'editBanner', params: { id: banner.id } }"
        class="banner_actions_button banner_actions_button--edit"
      >
        Редактировать
        <AppIcon icon="arrows-right" />
      </router-link>
      <button
        class="banner_actions_button banner_actions_button--delete"
        @click="deleteBanner"
      >
        Удалить
        <AppIcon icon="delete" size="0.65em" />
      </button>
    </div>
    <div
      class="banner_deleted"
      :class="{ 'is-visible': isDeleted || isBeingDeleted }"
    >
      <AppIcon v-if="isBeingDeleted" icon="loader-oval" size="2em" />
      <div v-else class="banner_deleted_info">
        <h4>Баннер удалён</h4>
        <AppButton
          @click="restoreBanner"
          color="primary"
          spinner
          :loading="isBeingRestored"
          >Восстановить</AppButton
        >
      </div>
    </div>
  </div>
</template>

<script>
import AppButton from '@/components/AppButton.vue';
import BannerCanvas from '@/components/BannerCanvas.vue';
import Client from '@/api/index';
import { useToaster } from '@/utils/useToaster';
import { useCategoriesStore } from '@/stores/useCategoriesStore';

export default {
  setup() {
    return {
      categoriesStore: useCategoriesStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    isDeleted: false,
    isBeingDeleted: false,
    isBeingRestored: false,
  }),
  components: {
    AppButton,
    BannerCanvas,
  },
  props: {
    banner: {
      type: Object,
      required: true,
    },
    width: {
      type: Number,
      required: true,
    },
  },
  computed: {
    categories() {
      return this.categoriesStore.categories.data;
    },
    category() {
      return (
        this.categories.find(
          (category) => category.id === this.banner.category_id,
        ) || {}
      );
    },
    canvas() {
      return this.banner?.data.canvases[0];
    },
  },
  methods: {
    async deleteBanner() {
      try {
        this.isBeingDeleted = true;
        await Client.banners.delete(this.banner.id);
        this.isDeleted = true;
      } catch (error) {
        this.toaster.error('Не получилось удалить баннер :(');
      } finally {
        this.isBeingDeleted = false;
      }
    },
    async restoreBanner() {
      try {
        this.isBeingRestored = true;
        await Client.banners.restore(this.banner.id);
        this.isDeleted = false;
      } catch (error) {
        this.toaster.error('Не получилось восстановить баннер :(');
      } finally {
        this.isBeingRestored = false;
      }
    },
  },
};
</script>

