<script>
import domtoimage from 'dom-to-image';
import { ColorPicker } from 'vue-color-kit';
import vSelect from 'vue-select';
import draggable from 'vuedraggable';

import LayerListItem from '@/components/LayerListItem.vue';
import BannerCopyright from '@/components/BannerCopyright.vue';
import AppRadioInput from '@/components/AppRadioInput.vue';
import AppInput from '@/components/inputs/AppInput/AppInput.vue';
import AppButton from '@/components/AppButton.vue';
import BannerCanvas from '@/components/BannerCanvas.vue';
import ModalColorScheme from '@/components/ModalColorScheme.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useCategoriesStore } from '@/stores/useCategoriesStore';
import { useBannersStore } from '@/stores/useBannersStore';
import { useToaster } from '@/utils/useToaster';
import Client from '@/api/index';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      categoriesStore: useCategoriesStore(),
      bannersStore: useBannersStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    zoom: 1,
    isFontColorPickerVisible: false,
    uploadProgress: 0,
    isSubmitting: false,
    isSubmitSuccess: '',
  }),
  props: ['id'],
  methods: {
    async getBanner() {
      try {
        await this.bannersStore.getBanner(this.id);
      } catch (error) {
        console.error(error);
        this.toaster.error('Не получилось загрузить баннер :(');
      }
    },
    editColorScheme(scheme) {
      this.bannersStore.selectColorScheme(scheme.id);
      this.appStore.showModalColorScheme(true);
    },
    addColorScheme() {
      this.appStore.showModalColorScheme(true);
    },
    switchColorScheme(id, e, schemeSettingsRef) {
      this.bannersStore.switchColorScheme(id, e, schemeSettingsRef);
    },
    selectCanvas(id) {
      this.bannersStore.setCanvasId(id);
      this.$nextTick(() => {
        this.scrollToCanvasCenter();
      });
    },
    scrollToCanvasCenter() {
      const scrollXShift =
        (this.canvas.width * 3 - document.body.clientWidth) / 2;
      return this.$refs.canvasScroller.scrollTo(scrollXShift, 0);
    },
    async submit() {
      if (this.isSubmitting) {
        return;
      }

      const {
        conversion,
        category,
        userGroup,
        canvases,
        schemes,
        layers,
        files,
      } = this.banner;

      try {
        this.isSubmitting = true;
        this.uploadProgress = 1;
        const config = {
          onUploadProgress: (progressEvent) => {
            const progress = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total,
            );
            this.uploadProgress = progress > 98 ? 98 : progress;
          },
        };
        // const scale = 2;
        // const canvasEl = this.$refs.canvas.$el.querySelector('.canvas');
        // const preview = await domtoimage.toPng(canvasEl, {
        //   // width: this.$refs.canvas.clientWidth * scale,
        //   // height: this.$refs.canvas.clientHeight * scale,
        //   // style: {
        //   //   transform: `scale(${scale})`,
        //   //   transformOrigin: 'top left',
        //   // },
        // });
        const preview =
          'data:image/png;base64,';
        const banner = {
          files,
          conversion,
          preview: preview.split(',')[1],
          category_id: category?.value,
          is_premium: userGroup?.value === 'premium',
          data: {
            canvases,
            schemes,
            layers,
          },
        };

        if (this.id) {
          banner.id = this.id;
          await Client.banners.update(banner, config);
          this.toaster.success('Изменения сохранены!');
        } else {
          await Client.banners.create(banner, config);
          this.$router.push({ name: 'adminBanners' });
          this.toaster.success('Баннер добавлен!');
        }
      } catch (error) {
        console.log(error);
        this.toaster.error('Не получилось сохранить :(');
      } finally {
        this.isSubmitting = false;
        this.uploadProgress = 0;
      }
    },
    async init() {
      this.appStore.toggleAppLoader(true);

      if (this.id) {
        await this.getBanner();
      } else {
        this.bannersStore.setUserGroup(0);
      }

      const preloadEl = this.$refs.canvas.$el.querySelector('.canvas_preload');
      await domtoimage.toPng(preloadEl);

      this.scrollToCanvasCenter();

      const toolbarCanvasWidth = this.$refs.toolbarCanvas.offsetWidth - 20;
      const canvasWidth = this.canvas.width;
      this.zoom = toolbarCanvasWidth / canvasWidth;

      document.addEventListener('click', this._clickListener);
      document.addEventListener('keydown', this._keyListener);

      setTimeout(() => {
        this.appStore.toggleAppLoader(false);
      }, 135);
    },
    changeZoom(key) {
      switch (key) {
        case '=':
          this.zoom += 0.05;
          break;

        case '-':
          this.zoom -= 0.05;
          break;

        default:
          this.zoom = 1;
      }
    },

    // Global event listeners
    _clickListener() {
      this.isFontColorPickerVisible = false;
    },
    _keyListener(e) {
      const {
        key,
        ctrlKey,
        target: { tagName: eventSource },
      } = e;
      const isTextInput = eventSource === 'INPUT' || eventSource === 'TEXTAREA';

      switch (key) {
        case '=':
        case '-':
        case '0':
          if (ctrlKey && !isTextInput) {
            e.preventDefault();
            this.changeZoom(key);
          }
          break;

        default:
          break;
      }
    },
  },
  computed: {
    banner() {
      return this.bannersStore.banner.data;
    },
    currentCanvasId() {
      return this.bannersStore.currentCanvasId;
    },
    currentSchemeId() {
      return this.bannersStore.currentSchemeId;
    },
    canvases() {
      return this.bannersStore.canvases;
    },
    canvas() {
      return this.bannersStore.canvas;
    },
    schemes() {
      return this.bannersStore.schemes;
    },
    layers() {
      return this.banner.layers.map((layer) => layer).reverse();
    },
    activeLayer() {
      return this.bannersStore.activeLayer;
    },
    categories() {
      return this.categoriesStore.categories.data;
    },
    listCategories() {
      return this.categories.map(({ id, name }) => {
        return {
          label: name,
          value: id,
        };
      });
    },
  },
  created() {
    this.bannersStore.addColorScheme(
      {
        color: '#2467EC',
        usedColors: [897544],
      },
      this.$refs.schemes,
    );
    this.bannersStore.switchColorScheme(this.schemes[0].id);
  },
  mounted() {
    this.init();
  },
  beforeUnmount() {
    this.bannersStore.resetState();
    document.removeEventListener('click', this._clickListener);
    document.removeEventListener('keydown', this._keyListener);
  },
  components: {
    BannerCopyright,
    AppRadioInput,
    AppInput,
    AppButton,
    draggable,
    vSelect,
    ColorPicker,
    LayerListItem,
    BannerCanvas,
    ModalColorScheme,
  },
  watch: {
    categories: {
      deep: true,
      immediate: true,
      handler() {
        this.banner.category = this.listCategories[0];
      },
