<template>
  <label class="radio">
    <input
      type="radio"
      :name="name"
      @input="update"
      :checked="modelValue === value"
    />
    <div class="radio_box"></div>
    <div class="radio_label">
      <slot />
    </div>
  </label>
</template>

<script>
export default {
  props: {
    name: {
      type: String,
    },
    value: {
      type: String,
    },
    modelValue: {
      type: String,
    },
  },
  methods: {
    update() {
      this.$emit('update:modelValue', this.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.radio {
  display: flex;
  position: relative;
  cursor: pointer;

  input {
    opacity: 0;
    position: absolute;
    width: 1px;
    height: 1px;
    z-index: -1;

    &:checked ~ .radio_box {
      &::before {
        opacity: 1;
        border-radius: 3px;
        transform: scale(1) rotate(2turn);
      }
    }
  }
  &_box {
    width: 20px;
    height: 20px;
    border: 2px solid #cfcfcf;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
      position: absolute;
      width: 12px;
      height: 12px;
      background: var(--color-accent);
      content: '';
      border-radius: 50%;
      opacity: 0;
      border-radius: 0;
      transition: 0.15s ease;
      transform: scale(0);
    }
  }
}
</style>
