<template>
  <div
    class="layerListItem"
    :class="{ 'is-active': layer.isActive }"
    @dblclick="enterTitleEditMode"
  >
    <div class="layerListItem_icon">
      <AppIcon :icon="layer.type === 'text' ? 'text' : 'gallery'" />
    </div>
    <span v-show="!isTitleEditMode">
      {{ layer.title || layer.text }}
    </span>
    <input
      type="text"
      v-show="isTitleEditMode"
      :value="layer.title || layer.text"
      @blur="setTitle"
      @keydown.enter="setTitle"
      @keydown.esc="exitTitleEditMode"
    />
  </div>
</template>

<script>
export default {
  data: () => ({
    isTitleEditMode: false,
  }),
  props: {
    layer: {
      type: Object,
      required: true,
    },
  },
  methods: {
    setTitle($event) {
      this.$emit('titleChange', $event.target.value);
      this.exitTitleEditMode();
    },
    enterTitleEditMode($event) {
      if (this.isTitleEditMode) return;

      this.isTitleEditMode = true;
      const input = $event.target
        .closest('.layerListItem')
        .querySelector('input');
      this.$nextTick(() => {
        input.select();
      });
    },
    exitTitleEditMode() {
      this.isTitleEditMode = false;
    },
  },
};
</script>

<style lang="scss" scoped>
