  display: flex;
  align-items: center;
  padding: 0.5em 25px;
  cursor: pointer;
  transition: background-color 0.075s ease;

  span {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  input {
    background: #fff;
    border: none;
    padding: 0 0.15em;
    color: var(--color-dark);
    font: inherit;
    width: 100%;
    height: 100%;
    outline: none;
    transform: translateX(-0.15em);
    border-radius: 3px;
  }
  & + & {
    border-top: 1px solid hsla(0, 0%, 0%, 0.05);
  }
  &:hover {
    background-color: hsla(0, 0%, 0%, 0.025);
  }
  &_icon {
    width: 1.25em;
    height: 1.25em;
    margin-right: 0.5em;
    flex-shrink: 0;
    color: #cfcfcf;

    .icon {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  &.is-ghost {
    background-color: hsla(0, 0%, 0%, 0.075);
  }
  &.is-active {
    background-color: var(--color-accent);
