<template>
  <div class="main_column users">
    <div class="users_header">
      <h2>Пользователи</h2>
      <AppButton color="primary" @click="showModalUser">
        Добавить пользователя
      </AppButton>
      <div class="users_header_download">
        <AppSelect
          :list="userCategories"
          v-model="userCategoryToDownload"
          :clearable="false"
        />
        <AppButton color="black" outlined>
          <img
            src="../../assets/images/charts.png"
            width="20"
            alt=""
            class="icon"
          />
          Скачать таблицу
        </AppButton>
      </div>
    </div>
    <div class="users_info">
      <div class="users_info_search">
        <AppInput
          placeholder="Ищете пользователя?"
          @input="debouncedSearchHander"
        />
        <AppIcon icon="search" :size="24" />
      </div>
      <div class="users_info_block">
        <div class="users_info_block_wrapper">
          <h4>270 000</h4>
          <div class="users_info_block_bottom">
            <span class="users_info_block_title">кол-во</span>
            <span class="users_info_block_new">+1 000</span>
          </div>
        </div>
      </div>
      <div class="users_info_block">
        <div class="users_info_block_wrapper">
          <h4>70 000</h4>
          <div class="users_info_block_bottom">
            <span class="users_info_block_title">премиум</span>
            <span class="users_info_block_new">+1 000</span>
          </div>
        </div>
      </div>
    </div>
    <table v-if="users.length" class="users_table">
      <thead>
        <th
          @click="setSortBy('name')"
          class="users_table_name"
          :class="{
            'is-sorted': this.sortBy === 'name',
            asc: this.sortBy === 'name' && this.sortDir === 1,
            desc: this.sortBy === 'name' && this.sortDir === -1,
          }"
        >
          <span>
            Имя
            <AppIcon icon="arrow-up" />
          </span>
        </th>
        <th
          @click="setSortBy('occupation')"
          class="users_table_occupation"
          :class="{
            'is-sorted': this.sortBy === 'occupation',
            asc: this.sortBy === 'occupation' && this.sortDir === 1,
            desc: this.sortBy === 'occupation' && this.sortDir === -1,
          }"
        >
          <span>
            Деятельность
            <AppIcon icon="arrow-up" />
          </span>
        </th>
        <th
          @click="setSortBy('email')"
          class="users_table_email"
          :class="{
            'is-sorted': this.sortBy === 'email',
            asc: this.sortBy === 'email' && this.sortDir === 1,
            desc: this.sortBy === 'email' && this.sortDir === -1,
          }"
        >
          <span>
            Email
            <AppIcon icon="arrow-up" />
          </span>
        </th>
        <th
          @click="setSortBy('download_count')"
          class="users_table_downloads"
          :class="{
            'is-sorted': this.sortBy === 'download_count',
            asc: this.sortBy === 'download_count' && this.sortDir === 1,
            desc: this.sortBy === 'download_count' && this.sortDir === -1,
          }"
        >
          <span>
            Скач.
            <AppIcon icon="arrow-up" />
          </span>
        </th>
        <th
          @click="setSortBy('time_wasted')"
          class="users_table_time"
          :class="{
            'is-sorted': this.sortBy === 'time_wasted',
            asc: this.sortBy === 'time_wasted' && this.sortDir === 1,
            desc: this.sortBy === 'time_wasted' && this.sortDir === -1,
          }"
        >
          <span>
            Времени
            <AppIcon icon="arrow-up" />
          </span>
        </th>
        <th class="users_table_actions"></th>
      </thead>
      <tbody>
        <tr class="users_table_notFound" v-if="!sortedUsers.length">
          <td colspan="5">
            <span>
              <img src="../../assets/images/emoji-sad-cat.png" alt="" />
              Не найдено
            </span>
          </td>
        </tr>
        <UserRowItem
          v-for="user in sortedUsers"
          :key="user.id"
          :user="user"
          @deleted="deleteUserHandler(user)"
          @restored="restoreUserHandler(user)"
        />
      </tbody>
    </table>
    <div v-else class="users_empty">
      <img src="../../assets/images/emoji-sad-cat.png" alt="" />
      <h3>Нет ни одного пользователя</h3>
    </div>
    <ModalUser />
  </div>
</template>

<script>
import AppSelect from '@/components/AppSelect.vue';
import AppInput from '@/components/inputs/AppInput/AppInput.vue';
import UserRowItem from '@/components/UserRowItem.vue';
import ModalUser from '@/components/ModalUser.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useUserStore } from '@/stores/useUserStore';
import debounce from 'lodash.debounce';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      userStore: useUserStore(),
      userCategories: [
        {
          label: 'Все пользователи',
          value: 'all',
        },
        {
          label: 'Премиум пользователи',
          value: 'premium',
        },
      ],
    };
  },
  data: () => ({
    userCategoryToDownload: '',
    sortBy: null,
    sortDir: 1,
    searchQuery: '',
  }),
  components: {
    AppInput,
    AppSelect,
    UserRowItem,
    ModalUser,
  },
  computed: {
    users() {
      return this.userStore.users.data;
    },
    filteredUsers() {
      const { searchQuery } = this;
      return searchQuery
        ? this.users.filter((user) => {
            return (
              user.name.toLowerCase().includes(searchQuery) ||
              user.email.toLowerCase().includes(searchQuery)
            );
          })
        : this.users;
    },
    sortedUsers() {
      const { sortBy } = this;
      const users = [...this.filteredUsers];
      return sortBy
        ? users.sort((a, b) => {
            const stringComparison = a[sortBy]?.localeCompare?.(b[sortBy]);
            const numberComparison = a[sortBy] - b[sortBy];
            return (stringComparison || numberComparison) * this.sortDir;
          })
        : users;
    },
  },
  methods: {
    async init() {
      this.appStore.toggleAppLoader(true);
      await this.userStore.getUsers();
      this.appStore.toggleAppLoader(false);
      this.userCategoryToDownload = this.userCategories[0];
    },
    showModalUser() {
      this.appStore.showModalUser(true);
    },
    setSortBy(sortBy) {
      if (this.sortBy === sortBy) {
        this.sortDir = this.sortDir === 1 ? this.sortDir * -1 : 0;
        this.sortBy = this.sortDir === 0 ? null : this.sortBy;
        return;
      }

      this.sortDir = 1;
      this.sortBy = sortBy;
    },
    debouncedSearchHander: debounce(function ($event) {
      this.searchQuery = $event.target.value.trim().toLowerCase();
    }, 300),
    deleteUserHandler(user) {
      user.isDeleted = true;
    },
    restoreUserHandler(user) {
      user.isDeleted = false;
    },
  },
  mounted() {
    this.init();
  },
};
</script>

