<template>
  <tr class="user">
    <td class="text user_name">
      <div class="user_name_wrapper">
        <img
          src="../assets/images/emoji-crown.png"
          class="icon"
          v-if="user.hasPremium"
          title="Премиум пользователь"
        />
        <img
          src="../assets/images/emoji-key.png"
          class="icon"
          v-if="user.admin_type"
          title="Администратор"
        />
        {{ user.name }}
      </div>
    </td>
    <td class="text">{{ user.occupation }}</td>
    <td class="text">{{ user.email }}</td>
    <td class="text">{{ user.download_count }}</td>
    <td class="text">
      {{ timeWasted ? timeWasted.toLocaleString(this.$i18n.locale) : '< 1' }}
      {{ $t('app.hoursShort') }}
    </td>
    <td class="user_actions">
      <AppButton
        class="user_actions_button"
        icon
        blank
        color="primary"
        @click="editUser"
      >
        <AppIcon icon="pencil" size="1em" />
      </AppButton>
      <AppButton
        class="user_actions_button"
        icon
        blank
        color="error"
        @click="deleteUser"
      >
        <AppIcon icon="delete" size="0.65em" />
      </AppButton>
      <div
        class="user_deleted"
        :class="{ 'is-visible': user.isDeleted || isBeingDeleted }"
      >
        <AppIcon v-if="isBeingDeleted" icon="loader-oval" size="1.5em" />
        <div v-else class="user_deleted_info text">
          <span>Пользователь удалён.</span>
          <AppButton
            @click="restoreUser"
            color="primary"
            spinner
            :loading="isBeingRestored"
            blank
          >
            Восстановить
          </AppButton>
        </div>
      </div>
    </td>
  </tr>
</template>

<script>
import { useToaster } from '@/utils/useToaster';
import AppButton from '@/components/AppButton.vue';
import { useUserStore } from '@/stores/useUserStore';
import { useAppStore } from '@/stores/useAppStore';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      userStore: useUserStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    isBeingDeleted: false,
    isBeingRestored: false,
  }),
  components: {
    AppButton,
  },
  props: {
    user: {
      type: Object,
      required: true,
    },
  },
  computed: {
    timeWasted() {
      const hours = Math.floor(this.user.time_wasted / 60 / 60);
      return hours;
    },
  },
  methods: {
    editUser() {
      this.userStore.selectUser(this.user);
      this.appStore.showModalUser(true);
    },
    async deleteUser() {
      try {
        this.isBeingDeleted = true;
        await this.userStore.deleteUser(this.user.id);
        this.$emit('deleted');
      } catch (error) {
        this.toaster.error('Не получилось удалить пользователя :(');
      } finally {
        this.isBeingDeleted = false;
      }
    },
    async restoreUser() {
      try {
        this.isBeingRestored = true;
        await this.userStore.restoreUser(this.user.id);
        this.$emit('restored');
      } catch (error) {
        this.toaster.error('Не получилось восстановить пользователя :(');
      } finally {
        this.isBeingRestored = false;
      }
    },
  },
};
</script>

