  width: 100%;
  position: relative;
  overflow: hidden;

  &:nth-child(odd) {
    td {
      background-color: #e7e7e7;
    }
  }
  td {
    padding: 1em 0.5em;
    line-height: 1.13em;

    &:first-child {
      border-top-left-radius: 15px;
      border-bottom-left-radius: 15px;
      padding-left: 1em;
    }
    &:last-child {
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      padding-right: 1em;
    }
  }
}
.user_name {
  .icon {
    width: 1.25em;
    margin-right: 0.5em;
  }
}
.user_name_wrapper {
  display: flex;
  align-items: center;
}
.user_actions {
  display: flex;
  align-items: center;
}
.user_actions_button {
  width: 24px;
  height: 24px;
}
.user_deleted {
  position: absolute;
  background: rgba(242, 242, 242, 0.75);
  backdrop-filter: blur(5px);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.125s ease;
  flex-direction: column;
  border-radius: 15px;

  .user:nth-child(odd) & {
    background: rgba(231, 231, 231, 0.75);
  }
  &.is-visible {
    opacity: 1;
    visibility: visible;
  }
}
.user_deleted_info {
  text-align: center;
  display: flex;
  align-items: center;
  font-weight: 600;

  .button {
    margin-left: 0.5em;
    font-size: 1em;
  }
}
</style>
